@import "variable";

@font-face {
  font-family: lato;
  src: local(lato),
       url(../webroot/fonts/AnyConv.com__Lato-Bold.woff2),
       url(../webroot/fonts/AnyConv.com__Lato-Bold.woff),
       url(../webroot/fonts/AnyConv.com__Lato-Regular.woff2),
       url(../webroot/fonts/AnyConv.com__Lato-Regular.woff),
}


body {
  font-family: "lato";
  text-align: center;
  font-style: normal;
  font-weight: normal;
  overflow: visible;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

ul {
  padding-left: 20px;
  padding-right: 20px;
}
